import React from 'react';
import  { Routes, Route } from 'react-router-dom';
import Intro from '../components/intro';
import Input, { CoefProvider } from '../components/input';
import Solve from '../components/solve';
import Steps from '../components/steps';

function Main() {
    return (
        <main>
            <div className='static-background'/>

            <Routes>
                <Route path="/" element={ <Intro /> }/>
                <Route exact path="/input" element={ <CoefProvider> <Input /> </CoefProvider>}/>
                <Route exact path="/solve/:method" element={ <CoefProvider> <Solve /> </CoefProvider> }/>
                <Route exact path="/solve/:method/steps" element={ <Steps /> }/>
            </Routes>
        </main>
    );
}

export default Main;